.container {
  display: flex;
  flex-direction: column;
  gap: 60px;
  justify-content: space-between;
}

.content {
  display: flex;
  flex-direction: column;
}

.title {
  font-family: "Jet Brains Mono", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  margin: 0;
}

.text {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}

.head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.list {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  height: 300px;
  flex-direction: column;
}
.li {
  list-style-type: none;
  color: #00cccc;
  padding: 0;
  margin-top: 8px;
  margin-right: 20px;
}

.li_upcoming {
  color: #f2f2f3;
  list-style-type: none;
  padding: 0;
  margin-top: 8px;
  margin-right: 20px;
}
